    mounted(){
        
        this.scrollReveal = scrollReveal({
            duration: 1500,
            reset: false,
            mobile: true,
            easing: 'linear',
        })
        this.$nextTick(() => {
            document.documentElement.scrollTop = 0;
            this.initEcharts();
