            this.scrollReveal.reveal('.dog-one__star', {
                origin: 'right',
                distance: '2rem',
                opacity: 0,
                scale: 2,
            });

            this.scrollReveal.reveal('.dog-one__fly', {
                origin: 'left',
                distance: '2rem',
                opacity: 0,
                scale: .5,
            });

            this.scrollReveal.reveal('.dog-title', {
                origin: 'top',
                distance: '1rem',
