                distance: '3rem',
                scale: 1,
            });

            this.scrollReveal.reveal('.dog-three__list', {
                origin: 'right',
                distance: '3rem',
                scale: 1,
            });

            this.scrollReveal.reveal('.dog-five__left', {
                origin: 'left',
                distance: '3rem',
                afterReveal: (el) => {
                    el.style.transition = 'all .3s';
                }
            });

            this.scrollReveal.reveal('.dog-five__right', {
                origin: 'right',
                distance: '3rem',
                afterReveal: (el) => {
                    el.style.transition = 'all .3s';
                }
            });

            this.scrollReveal.reveal('.dog-five__desc', {
                origin: 'bottom',
                distance: '1rem',
                scale: 2,
