                origin: 'bottom',
                distance: '1rem',
                scale: 2,
            });
        })
    },
    methods: {
        goRouterPath(name) {
            this.$router.push({
                name
            })
        },
        getClass(index) {
            switch (index) {
                case 0:
                    return 'one';
                case 1:
                    return 'two';
                case 2:
                    return 'three';
                case 3:
                    return 'four';
                case 4:
                    return 'five';
                case 5:
                    return 'six';
                case 6:
                    return 'seven';
            }
        },

        initEcharts() {
            // 
            Highcharts.chart(this.$refs.charts, {
                chart: {
                    type: 'pie',
                    backgroundColor: 'rgba(0,0,0,0)',
                    options3d: {
                        enabled: true,
                        alpha: 45,
                        beta: 0
                    }
                },
                title: {
                    text: null,
                },
                exporting: {
                    enabled: false
                },
                tooltip: {
                    pointFormat: '{series.name}: <b>{point.percentage:.1f}%</b>'
                },
                plotOptions: {
                    pie: {
                        allowPointSelect: true,
                        cursor: 'pointer',
                        depth: 35,
                        dataLabels: {
                            enabled: true,
                            format: '{point.name}'
                        }
                    }
                },
                series: [{
                    type: 'pie',
                    name: 'ratio',
                    data: this.chartsData
                }]
            });
        },
        toggleTab(index) {
            this.tabIndex = index
        },
        navClick(index) {
            switch (index) {
                case 1:
                    this.$refs.banner.scrollIntoView({
                        behavior: 'smooth',
                    });
                    break;
                case 2:
                    this.$refs.tokenomics.scrollIntoView({
                        behavior: 'smooth',
                        block: 'start'
                    });
                    break;
                case 3: 
                    this.$refs.roadmap.scrollIntoView({
                        behavior: 'smooth',
                        block: 'start'
                    });
                    break;
            }
        }
    }
}
</script>

